body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: "Inter", sans-serif;
  font-style: normal;
}

.main {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url('./assets/background.png');
  background-size: 100% 100%;
  position: relative;
}

.player {
  position: absolute;
  width: 7.8vw;
  height: 22vh;
}

.player::before {
  content: '';
  display: block;
  position: absolute;
  background-image: url('./assets/brackets.png');
  background-size: 100% 100%;
  width: 0;
  height: 0;
  left: 50%;
  top: 50%;
  transition: width .3s ease,
              height .3s ease,
              left .3s ease,
              top .3s ease;
  z-index: 1;
}

.active.player::before {
  width: 8.8vw;
  height: 22vh;
  top: 0;
  left: -0.5vw;
}

.playerImg {
  width: 100%;
  height: 100%;
  z-index: 2;
}

.playerPosition {
  position: absolute;
  font-size: 1.8vh;
  line-height: 100%;
  font-weight: 700;
  bottom: -2.2vh;
  color: white;
  left: 50%;
  transform: translate(-50%, 0);
}

.player:nth-child(1) {
  bottom: 9vh;
  left: 50%;
  transform: translate(-50%, 0);
}

.player:nth-child(2) {
  bottom: 21vh;
  left: 27vw;
}

.player:nth-child(3) {
  bottom: 21vh;
  right: 27vw;
}

.player:nth-child(4) {
  bottom: 26vh;
  left: 10.5vw;
}

.player:nth-child(5) {
  bottom: 26vh;
  right: 10.5vw;
}

.player:nth-child(6) {
  bottom: 43vh;
  left: 50%;
  transform: translate(-50%, 0);
}

.player:nth-child(7) {
  bottom: 50vh;
  left: 22vw;
}

.player:nth-child(8) {
  bottom: 50vh;
  right: 22vw;
}

.player:nth-child(9) {
  bottom: 70vh;
  left: 32vw;
}

.player:nth-child(10) {
  bottom: 70vh;
  right: 32vw;
}

.player:nth-child(11) {
  bottom: 76vh;
  left: 50%;
  transform: translate(-50%, 0);
}

.selectPanel {
  position: absolute;
  height: 52vh;
  bottom: -52vh;
  left: 0;
  width: 100vw;
  background: #05182E80;
  padding: 3.7vh 4.5vw 10vh 4.5vw;
  transition: bottom .3s ease;
  box-sizing: border-box;
  z-index: 10;
}

.selectPanel.open {
  bottom: 0;
}

.selectPanelRow {
  display: flex;
  justify-content: space-between;
}

.selectPlayer {
  width: 10vw;
  height: 28.3vh;
}

.topLeft {
  position: absolute;
  left: 2vw;
  top: 8hv;
}

.logo {
  width: 8vw;
  height: auto;
  margin-bottom: 4vh;
}

.fieldTitle, .textField {
  font-size: 4.5vh;
  font-family: "Roboto", sans-serif;
  font-style: italic;
  font-weight: 900;
  line-height: 100%;
  color: white;
  margin-bottom: 1.4vh;
  background: transparent;
}

.textField {
  margin-bottom: 0;
  border: none;
  outline: none !important;
}

.reset {
  position: absolute;
  left: 2vw;
  bottom: 3.7vh;
  font-size: 2.7vh;
  font-family: "Roboto", sans-serif;
  font-style: italic;
  font-weight: 900;
  line-height: 100%;
  display: flex;
  align-items: center;
  color: white;
  background: transparent;
  border: none;
}

.resetImage {
  width: 2.2vw;
  height: 4vh;
  margin-right: 1vw;
}

.selectPanelTitle {
  width: 100%;
  text-align: center;
  font-size: 6.4vh;
  line-height: 100%;
  font-family: "Roboto", sans-serif;
  font-style: italic;
  font-weight: 900;
  margin-bottom: 1vh;
  color: white;
}

.selectPanelPosition {
  width: 100%;
  text-align: center;
  font-size: 2.7vh;
  font-weight: 700;
  line-height: 100%;
  margin-bottom: 3.1vh;
  color: white;
}